<!--办理方式-->
<template>
  <div class="blfs-list"
       v-loading="!data.length"
       :element-loading-background="$STANDARD.loadingBgColor">
    <ul>
      <li
        v-if="data.length"
        class="item"
        v-for="(item,index) of data"
        :key="index"
        :style="{
        top:(data.length%2 ===0?evenPosArr[index].top:oddPosArr[index].top)+'px',
        left:(data.length%2 ===0?evenPosArr[index].left:oddPosArr[index].left)+'px',
        }"
      >
        <p class="name" :style="{lineHeight:item.name.length>2?'20px':'40px'}">{{item.name}}</p>
        <p class="value">{{item.value}}</p>
        <p class="unit">件次</p>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: "blfsIcon",
    props: {
      data: Array,
      // type:{
      //   type:String,
      //   default:'odd'
      // }
    },
    data() {
      return {
        // 此处只有7项数据和8项数据之分，奇数：odd,偶数：even
        oddPosArr: [
          {
            "top": 40,
            "left": 80,
          },
          {
            "top": 40,
            "left": 260,
          },
          {
            "top": 40,
            "left": 440,
          },
          {
            "top": 170,
            "left": 0,
          },
          {
            "top": 170,
            "left": 180,
          },
          {
            "top": 170,
            "left": 360,
          },
          {
            "top": 170,
            "left": 540,
          },
        ],
        evenPosArr: [
          {
            "top": 40,
            "left": 0,
          },
          {
            "top": 40,
            "left": 180,
          },
          {
            "top": 40,
            "left": 360,
          },
          {
            "top": 40,
            "left": 540,
          },
          {
            "top": 170,
            "left": 0,
          },
          {
            "top": 170,
            "left": 180,
          },
          {
            "top": 170,
            "left": 360,
          },
          {
            "top": 170,
            "left": 540,
          },
        ]
      }
    },
    computed: {
      styleType() {
        return this.$store.state.styleType;
      }
    },
  }
</script>

<style lang="stylus" scoped>
  .blfs-list
    position relative
    width 700px
    li.item
      position absolute
      width 150px
      height 85px
      background url("./img/blfs-icon.png") no-repeat
      .name
        position absolute
        top 22px
        left 22px
        width 40px
        font-size 16px
        color #fff
        text-align center
      .value
        position absolute
        top 30px
        right 12px
        font-size 16px
        color #ffde00
        font-weight 700
      .unit
        position absolute
        bottom 20px
        right 12px
        font-size 14px
        color #12dded
</style>
