<template>
  <div class="wrapper">
    <div class="w-seach" @click="handlePopShow">
      <p :title="curName.length>6?curName:''">{{curName}}</p>
    </div>
    <transition name="fade">
      <depSeach v-if="popShow" :name="curName" @change="popClick"></depSeach>
    </transition>

    <div class="content">
      <div class="c-top">
        <div class="part ct-left">
          <ctitle :title="'办理方式'"></ctitle>
          <div class="inner">
            <blfsList :data="blfsListData"></blfsList>
          </div>
        </div>
        <div class="part ct-right">
          <ctitle :title="'受理量走势'"></ctitle>
          <div class="inner">
            <timeChange class="timeChange" @change="zsTimeClick"></timeChange>
            <LinesR :params="zsLineParams"></LinesR>
          </div>
        </div>
      </div>
      <div class="c-bottom">
        <div class="part cb-left">
          <ctitle :title="'信访形式'"></ctitle>
          <div class="inner">
            <pie-chart3D :params="xfxsPieParams"></pie-chart3D>
          </div>
        </div>
        <div class="part cb-center">
          <ctitle :title="'投诉问题占比'"></ctitle>
          <div class="inner">
            <loopPie :params="tswtzbPieParams"></loopPie>
          </div>
        </div>
        <div class="part cb-right">
          <ctitle :title="'信访评价'"></ctitle>
          <div class="inner">
            <timeChange class="timeChange" @change="xfpjTimeClick"></timeChange>
            <LinesR :params="xfpjLineParams"></LinesR>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import depSeach from "@/components/depSeach/index"
  import {get_http} from "@/api/index";
  import {gradientColor} from "@/utils/gradient";
  import timeChange from "@/components/timeChange/index";
  import ctitle from "@/components/title/index";
  import loopPie from "@/components/pie/LoopPie";
  import PieChart3D from "@/components/pie/PieChart3D";
  import BScroll from "better-scroll";
  import LinesR from "@/components/line/LinesR";
  import blfsList from "@/components/list/blfsList";

  export default {
    name: "xfbm",
    components: {
      depSeach,
      ctitle,
      loopPie,
      PieChart3D,
      LinesR,
      timeChange,
      blfsList
    },
    data() {
      return {
        popShow: false,
        curName: "部门索引",
        listScroll: null,
        //办理方式
        blfsListData: [],
        // 信访量走势
        zsLineParams: {
          data: [],
          colors: [
            // 紫
            {
              line: "rgba(220, 150, 209, 1)",
              start: "rgba(220, 150, 209, 0.3)",
              end: "rgba(220, 150, 209, 0)"
            },
          ],
          option: {
            splitLineShow: true,
            legendShow: false,
            legenditemWidth: 22,
            legenditemHeight: 4,
            axisLabelShow: true,
            axisTickShow: false,
            axisCategoryName: "年",
            axisYName: "件次",
            axisLabelSize: 14,
            scales: true,
            axisLabelFontSize: 16,
            yaxisLabelFontSize: 14,
            Tofixed: false,
            smooth: false,
            areaShow: 1,
            smoothMonotone: "none",
            dataZoomshow: false,
            legendIcon: "rect",
            left: "8%",
            right: "12%",
            top: "15%",
            bottom: "10%",
            legendTop: "2%",
            legendRight: "12%"
          },
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          map: {
            "value": "数量",
          },
          xName: "name"
        },
        //投诉问题占比
        tswtzbPieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            bgSize: 220,
            colors: this.$STANDARD.pieColors,
            radius: ["45%", "55%"],
            center: ["50%", "50%"],
            centerRadius: ["40%", "41%"],
            labelRich: {
              b: this.fontSize(0.18),
              d: this.fontSize(0.2),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 70,
            // borderColor: "#050f28",
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "16%" //文字大小
            },
            labelSize: "24%", // label字号
            rollFlag: true,
            threeLevel: true,
          }
        },
        // 信访形式
        xfxsPieParams: {
          data: [],
          color: ["#5166c1", "#32b1e0", "#15bbc2"],
          angle: 20,
          radius: "30%",
          labelRadius: 35,
          styleObj: {
            width: "100%",
            height: "100%"
          },
          isBr: true,
          labelPer: true
        },
        // 信访评价
        xfpjLineParams: {
          data: [],
          colors: [
            {
              line: "#e11770",
              start: "rgba(225, 23, 112, .5)",
              end: "rgba(225, 23, 112, .5)"
            },
            {
              line: "#ffb435",
              start: "rgba(255, 180, 53, .5)",
              end: "rgba(255, 180, 53, .5)"
            },
            {
              line: "#1a82f7",
              start: "rgba(26, 130, 247, .5)",
              end: "rgba(26, 130, 247, .5)"
            },
            {
              line: "#a1d871",
              start: "rgba(161, 216, 113, .5)",
              end: "rgba(161, 216, 113, .5)"
            },
            {
              line: "#ff825b",
              start: "rgba(255, 130, 91, .5)",
              end: "rgba(255, 130, 91, .5)"
            }
          ],
          option: {
            splitLineShow: true,
            legenditemWidth: 22,
            legenditemHeight: 4,
            axisLabelShow: true,
            axisTickShow: false,
            axisYName:"%",
            axisCategoryName: "年",
            axisLabelSize: 14,
            scales: true,
            axisLabelFontSize: 16,
            yaxisLabelFontSize: 14,
            Tofixed: false,
            smooth: true,
            areaShow: 0,
            smoothMonotone: "none",
            dataZoomshow: false,
            legendIcon: "rect",
            left: "8%",
            right: "12%",
            top: "15%",
            bottom: "10%",
            legendTop: "2%",
            legendRight: "12%"
          },
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          map: {},
          xName: "name"
        }
      };
    },
    mounted() {
      this.getAPI(this.year);
    },
    methods: {
      handlePopShow() {
        this.popShow = true;
      },
      // 点击弹框后触发
      popClick(name) {
        this.curName=(name === ''?'部门索引':name);
        this.getAPI(this.year);
      },
      getAPI(year) {
        let xfbmParam=(this.curName === '部门索引' ? 'drop' : this.curName);
        // 1办理方式
        get_http("jghxXfbmAbmBlfsList", {xfbm: xfbmParam, year: year})
          .then(res => {
            this.blfsListData = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 2信访量走势
        this.getZsLineAPI(year, '年度');
        // 3信访形式
        get_http("jghxXfbmAbmXfxsPie", {xfbm: xfbmParam, year: year})
          .then(res => {
            this.xfxsPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 4投诉问题占比
        get_http("jghxXfbmAbmTswtzbPie", {xfbm: xfbmParam, year: year})
          .then(res => {
            this.tswtzbPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 5信访评价
        this.getXfpjLineAPI(year, '年度');
      },
      // 3信访量走势
      getZsLineAPI(year, time) {
        let timetype = '', _year = '';
        if (time === '年度') {
          timetype = 'cy';
          _year = '[now-4, now]'
        } else {
          timetype = 'cm';
          _year = year
        }
        let xfbmParam=(this.curName === '部门索引' ? 'drop' : this.curName);
        get_http("jghxXfbmAbmZsLine", {
          year: _year,
          xfbm: xfbmParam,
          timetype: timetype
        })
          .then(res => {
            this.zsLineParams.option.axisCategoryName = (time === '年度' ? '年' : '月');
            this.zsLineParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 6信访评价
      getXfpjLineAPI(year, time) {
        let timetype = '', _year = '';
        if (time === '年度') {
          timetype = 'cy';
          _year = '[now-4, now]'
        } else {
          timetype = 'cm';
          _year = year
        }
        let xfbmParam=(this.curName === '部门索引' ? 'drop' : this.curName);
        get_http("jghxXfbmAbmXfpjLine", {
          year: _year,
          xfbm: xfbmParam,
          timetype: timetype
        })
          .then(res => {
            this.xfpjLineParams.option.axisCategoryName = (time === '年度' ? '年' : '月');
            this.xfpjLineParams.map = res.data.map;
            this.xfpjLineParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 信访量走势-时间切换
      zsTimeClick(time) {
        this.getZsLineAPI(this.year, time);
      },
      // 信访评价-时间切换
      xfpjTimeClick(time) {
        this.getXfpjLineAPI(this.year, time);
      },
      fontSize(res) {
        let docEl = document.documentElement,
          clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      }
    },
    computed: {
      year() {
        return this.$store.state.year;
      }
    },
    watch: {
      year(val) {
        this.getAPI(val);
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)
    .fade-enter-active, .fade-leave-active
      transition opacity .3s
    .fade-enter, .fade-leave-to
      opacity 0
    .w-seach
      position absolute
      top -78px
      right 113px
      z-index 1000
      box-sizing border-box
      padding-left 42px
      width 262px
      line-height 62px
      font-size 22px
      color #cbfbff
      background url("../../../assets/seach-bg.png") no-repeat
      cursor pointer
      p
        width 150px
        text-align center
        white-space nowrap
        overflow hidden
        text-overflow ellipsis
    .content
      position relative
      z-index 1
      width 100%
      height 100%
      display flex
      flex-direction column
      padding .2rem .3rem
      box-sizing border-box
      .part
        position relative
        flex 1
        width 100%
        height 100%
        .inner
          display flex
          justify-content center
          height calc(100% - 0.28rem)
          .chart
            width 100%
            height 100%
          .timeChange
            top 0
            right 1rem
      .c-top
        flex 1
        display flex
        position relative
        .ct-left
          flex 1
        .ct-right
          flex 1

      .c-bottom
        flex 1
        display flex
        .cb-left
          flex 1
        .cb-center
          flex 1
        .cb-right
          flex 2

</style>
